<template>
    <ul class="footer-low">
        <template  v-for="(item, index) in footLowItems" >
                <li :key="index">
                    <router-link :to="'/' + item.baseLink" class="footer-low-link">{{item.name}}</router-link>
                </li>
                <li :key="index" v-if="!isLast(index)" class="footer-low-divider">
                    <i class="fas fa-circle"/>
                </li>
        </template>
    </ul>
</template>


<script>
import footLowItems from "../../data/lower-footer-data.json";

export default {
    name: 'FooterLowComponent',
    data: function () {
        return {
            footLowItems
    }
  },
  methods: {
      isLast: function (index) {
      return footLowItems.length === (index + 1);
    }
  }

}

</script>


<style lang="scss">
@import "src/styles/variables";

@media screen and (min-width: $breakpoint-m) {
    
    .footer-low{
        display: flex;
        justify-content: center;
        align-items: center;
        color: #5A348F;
        margin-bottom: 1.5rem;

        &-divider{
            font-size: .2rem;
             margin: .5rem .5rem ;
        }

        &-link{

            a {
                &, &:visited, &:focus, &:hover{
                    color: #5A348F
                 }
            }
            
        }
    }
}
</style>