import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/faq',
    name: 'Faq',
    component: () => import('../views/Faq.vue')
  },
  { 
    path: '/portfolio', 
    redirect: '/portfolio/digital-art' 
  },
  {
    path: '/portfolio/analog-art',
    name: 'AnalogArt',
    component: () => import('../views/portfolio/AnalogArt.vue')
  },
  {
    path: '/portfolio/digital-art',
    name: 'DigitalArt',
    component: () => import( '../views/portfolio/DigitalArt.vue')
  },
  { 
    path: '/about', 
    redirect: '/about/my-story' 
  },
  {
    path: '/about/my-story',
    name: 'MyStory',
    component: () => import('../views/about/MyStory.vue')
  },
  {
    path: '/about/social-media',
    name: 'SocialMedia',
    component: () => import('../views/about/SocialMedia.vue')
  },
  { 
    path: '/commissions', 
    redirect: '/commissions/procedure' 
  },
  {
    path: '/commissions/price',
    name: 'Price',
    component: () => import('../views/commissions/Price.vue')
  },
  {
    path: '/commissions/procedure',
    name: 'Procedure',
    component: () => import('../views/commissions/Procedure.vue')
  },
  {
    path: '/terms',
    name: 'Terms',
    component: () => import('../views/footer/Terms.vue')
  },
  {
    path: '/legal-notice',
    name: 'LegalNotice',
    component: () => import('../views/footer/LegalNotice.vue')
  },
  {
    path: '/privacy-policy',
    name: 'PrivacyPolicy',
    component: () => import('../views/footer/PrivacyPolicy.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
