<template>
  <header class="header">
    <div class="banner">
      <label class="burger-menu">
        <i class="fas fa-fw burger-menu-icon" :class="open ? 'fa-times' : 'fa-bars'" />
        <input v-model="open" class="burger-menu-checkbox" type="checkbox">
      </label>
      <router-link class="banner-logo" to="/"><img alt="Logo" src="../../assets/LogoWhite.png"></router-link>
    </div>
    <NavComponent :open="open"></NavComponent>
  </header>
</template>

<script>
import NavComponent from "./NavComponent";

export default {
  name: "HeaderComponent",
  components: {NavComponent},
  data: function () {
    return {
      open: false
    }
  },
  watch: {
    '$route': function (from, to) {
      if (from !== to) this.open = false;
    }
  }
}
</script>

<style lang="scss">
@import "src/styles/variables";
.header{
  width: 100%;
}
.banner {
  width: 100%;
  max-height: 15%;
  background-image: url("../../assets/Banner.png");
  background-repeat: no-repeat;
  background-size: cover;

  display: flex;
  align-items: center;

  &-logo {
    display: flex;
    justify-content: center;

    & > img {
      max-width: 50%;
    }
  }
}

.burger-menu {
  &-checkbox {
    display: none;
  }

  &-icon {
    color: white;
    font-size: 2.5rem;
    padding: 0 .5rem;

    transition: all 1s ease;

    &.open {
      display: none;
    }
  }
}

@media screen and (min-width: $breakpoint-m) {
  .banner {
    display: grid;
    align-items: center;
    grid-template-columns: auto 30% auto;
    max-height: initial;

    &-logo {
      grid-column: 2;

      & > img {
        max-width: 100%;
      }
    }
  }

  .burger-menu {
    display: none;
  }
}
</style>
