<template>
  <div class="nav-item">
    <div v-if="!IsExternaLink">
      <router-link
        v-if="!hasSubNav"
        :to="'/' + baseLink"
        class="nav-link nav-text"
        >{{ name }}</router-link
      >
      <div v-else class="nav-dropdown">
        <div class="nav-text" @click="toggleOpen">{{ name }}</div>
        <ul class="nav-dropdown-container" :class="{ open }">
          <template v-for="(item, index) in items">
            <li :key="index">
              <router-link
                :to="getRouterLink(item.value)"
                class="nav-link nav-dropdown-link"
                >{{ item.key }}</router-link
              >
            </li>
            <li :key="index" v-if="!isLast(index)" >
              <div class="nav-dropdown-divider"></div>
            </li>
          </template>
        </ul>
      </div>
    </div>
    <a v-else :href="baseLink" class="nav-link nav-text">{{ name }}</a>
  </div>
</template>

<script>
export default {
  name: "NavItemComponent",
  props: {
    name: String,
    baseLink: String,
    items: Array,
  },
  data: function () {
    return {
      open: false,
    };
  },
  computed: {
    hasSubNav: function () {
      return (
        this.items !== null && this.items !== undefined && this.items.length > 0
      );
    },
    IsExternaLink: function () {
      return this.baseLink.startsWith("https://");
    },
  },
  methods: {
    getRouterLink: function (itemLink) {
      return `/${this.baseLink}/${itemLink}`;
    },
    toggleOpen: function () {
      this.open = !this.open;
    },
    isLast: function (index) {
      return this.items.length === index + 1;
    },
  },
  watch: {
    $route: function (from, to) {
      if (from !== to) this.open = false;
    },
  },
};
</script>

<style lang="scss">
@import "src/styles/variables";

.nav-text {
  padding-left: 0.5rem;
}

.nav-item {
  padding: 0.5rem 0;
  & a {
    display: block;
  }
}

.nav-dropdown-container {
  height: 0;
  overflow: hidden;
  transition: transform 0.25s ease;
  transform-origin: top;
  transform: scaleY(0);
  background-color: #eeeeee;
  position: relative;

  & a {
    padding: 0.5rem 0 0.5rem 1.5rem;
  }

  &.open {
    transform: scaleY(1);
    height: auto;
  }
}

@media screen and (min-width: $breakpoint-m) {
  .nav-text {
    padding-left: 0rem;
  }
  .nav-item {
    width: max-content;
    padding: 0.5rem 0.5rem;

    &:hover {
      background-color: #b5b2e6;

      & .nav-dropdown-container {
        display: block;
      }
    }
  }
  .nav-dropdown {
    position: relative;
    a {
      padding: 0.5rem 1.5rem;
      width: max-content;
      &,
      &:visited,
      &:focus {
        color: white;
      }
    }
    &-divider {
      border: 0.1rem groove #2B2A40;
      margin: 0 .5rem;

              &:hover {
          background-color: #504f64;
        }
    }
    &-container {
      margin-top: 0.5rem;
      left: -0.5rem;
      height: auto;
      width: 15rem;
      position: absolute;
      display: none;
      transform: scaleY(1);
      background-color: #504f64;

      li {
        &:hover {
          background-color: #b5b2e6;
        }
      }

      &.open {
        transform: none;
      }
    }
  }
}
</style>
