<template>
  <nav :class="{open}">
    <NavItemComponent v-for="(item, index) in navItems" :key="index" v-bind="item" @linkClicked="$emit('linkClicked')"/>
  </nav>
</template>

<script>
import NavItemComponent from "./NavItemComponent";
import navItems from "../../data/navigation-data.json";

export default {
  name: "NavComponent",
  components: {NavItemComponent},
  props: {
    open: Boolean
  },
  data: function () {
    return {
      navItems
    }
  }
}
</script>

<style lang="scss">
@import "src/styles/variables";

nav {
  display: flex;
  flex-direction: column;

  width: 60%;
  position: absolute;
  min-height: 100%;
  left: -110%;

  transition: left .5s ease;
  background: white;
  font-size: 1.5rem;
  color: #320065;

  a {
    &, &:visited, &:hover, &:focus {
      color: #320065;
    }
  }

  box-shadow: 0.8rem .5rem  .8rem #DFDAE4;
  &.open {
    left: 0;
  }
}

@media screen and (min-width: $breakpoint-m) {
  nav {
    box-shadow: 0 .5rem  .8rem #DFDAE4;
    width: 100%;
    justify-content: center;
    flex-direction: row;
    position: initial;

  }
}

</style>

